import React from "react";
import { Link } from "react-router-dom";
import { CHAT__ROUTE, GIT__NAME__ROUTE } from "../../Routs/SiteRoutes";
import { SignOutButton } from "../SignOutButton";
import baseUserImgUrl from "../../Img/user.png";
import Avatar from "react-avatar-edit";
import baseUserProfileImg from "../../Img/user.png";
import "./user-edit.css";
import { useState } from "react";
import { UserAuth } from "../../contexts/AuthContext";
import { ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";
import { useEffect } from "react";
import { async } from "@firebase/util";
import { db, storage } from "../../utils/firebase";
import { doc, updateDoc } from "firebase/firestore";

export const UserEdit = () => {
  const curentUser = UserAuth();
  const [err, setErr] = useState(null)
  const [isModalActive, setIsModalActive] = useState(false);
  const [modalErr, setModalErr] = useState(null);
  const [imgCrop, setImgCrop] = useState();
  const [storeImg, setStoreImg] = useState();

  

  const closeModal = (e) => {
    if (e.target.dataset.close) {
      setIsModalActive(false);
    }
  };
  

  const onFileLoad = (file) => {
    if (file.type.substring(0, 5) !== "image") {
      file.target.value = "";
    }
  };

  const onBeforeFileLoad = (file) => {
    if (file.target.files[0].size > 10000000) {
      setModalErr("файл занадто великий");
      file.target.value = "";
    } else {
      setModalErr(false);
    }
  };

  const onCrop = (view) => {
    setImgCrop(view);
  };

  const onClose = () => {
    setImgCrop(null);
  };

  const saveImg = async (e) => {
    e.target.disabled = true;
    const userRef = doc(db, "users", curentUser.uid)
    await updateDoc(userRef, {
      photoUrl: imgCrop
    })
    // const storageRef = ref(storage, ``);
    // const uploadTask = uploadBytesResumable(storageRef, imgCrop);
    // const userRef = doc(db, "users", curentUser.uid)
    // uploadTask.on(
    //   (error) => {
    //     setErr(error)
    //   },
    //   () => {

    //     getDownloadURL(uploadTask.snapshot.ref).then(async (downloadURL) => {
    //         await updateDoc(userRef,{           
    //           photoUrl: downloadURL
    //         })
    //     });
    //   }
    // );
    e.target.disabled = false;
    setStoreImg(imgCrop);
    setIsModalActive(false);
  };

  return (
    <div className="user-edit">
      <div className="user-edit__container container">
        <div className="user-edit__wrapper">
          {/* <SignOutButton/>
                <Link to={CHAT__ROUTE}> Перейти до чату
                </Link> */}
          <div className="user-edit__title">редагувати профіль</div>
          <div className="user-edit__photo-editor">
            {/* <input type="file" id="user-edit__input-img" accept="image/*" onChange={e =>
            uploadProfilePhoto(e)}/>
            <label
              htmlFor="user-edit__input-img"
              className="user-edit__img-label"
            >
              <img src={baseUserImgUrl} alt="" />
            </label>
            <label
              htmlFor="user-edit__input-img"
              className="user-edit__text-label"
            >
              змінити фото профілю
            </label> */}
            <div
              className="user-edit__img-label"
              onClick={() => setIsModalActive(true)}
            >
              <img
                src={
                  storeImg
                    ? storeImg
                    : (curentUser && curentUser.photoURL) || baseUserProfileImg
                }
                alt=""
              />
            </div>
            <div
              className="user-edit__text-label"
              onClick={() => setIsModalActive(true)}
            >
              змінити фото профілю
            </div>
            {err && (<p className="user-edit__errMes">{err}</p>)}
          </div>
          <div
            className={
              `${isModalActive ? "add-photo-modal-active" : ""}` +
              " user-edit__add-photo-modal" +
              " add-photo-modal"
            }
            data-close="true"
            onClick={(e) => closeModal(e)}
          >
            <div className="add-photo-modal__window">
              <div className="add-photo-modal__title">
                Виберіть нове зображення
              </div>
              <img
                src={
                  imgCrop ||
                  (curentUser && curentUser.photoURL) ||
                  baseUserProfileImg
                }
                alt=""
                className="add-photo-modal__preview"
              />
              <div className="add-photo-modal__avatar">
                <Avatar
                  width={290}
                  height={290}
                  imageWidth={290}
                  onCrop={onCrop}
                  onClose={onClose}
                  onBeforeFileLoad={onBeforeFileLoad}
                  onFileLoad={onFileLoad}
                />
              </div>
              {modalErr && (
                <p className="add-photo-modal__error-message">{modalErr}</p>
              )}
              <button
                className="add-photo-modal__save-btn btn"
                onClick={saveImg}
              >
                зберегти
              </button>
            </div>
          </div>
          <SignOutButton/>
        </div>
      </div>
    </div>
  );
};
